@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700);
@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*
    DEMO STYLE
*/
body {
  font-family: "Poppins", sans-serif;
  background: #fafafa;
}

p {
  font-family: "Poppins", sans-serif;
  font-size: 1.1em;
  font-weight: 300;
  line-height: 1.7em;
  color: #999;
}

a,
a:hover,
a:focus {
  color: inherit;
  text-decoration: none;
  transition: all 0.3s;
}

.navbar {
  padding: 15px 10px;
  background: #fff;
  border: none;
  border-radius: 0;
  margin-bottom: 40px;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
}

.navbar-btn {
  box-shadow: none;
  outline: none !important;
  border: none;
}

.line {
  width: 100%;
  height: 1px;
  border-bottom: 1px dashed #ddd;
  margin: 40px 0;
}
.pad-takeinput {
  margin-right: 5px;
}
.logoutbtn {
  margin-left: 5px;
}

/* ---------------------------------------------------
    SIDEBAR STYLE
----------------------------------------------------- */

.wrapper {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-align-items: stretch;
          align-items: stretch;
}

.sidebar {
  position: fixed;
  min-width: 250px;
  max-width: 250px;
  background: #7386d5;
  color: #fff;
  transition: all 0.3s;
  z-index: 1;
  height: 100%;
}

.sidebar-active {
  margin-left: -250px;
}

.sidebar .sidebar-header {
  padding: 20px;
  background: #6d7fcc;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}
.sidebar-header img {
  border-radius: 10%;
  height: 60px;
  margin-right: 10px;
}

.sidebar ul.components {
  padding: 20px 0;
  border-bottom: 1px solid #47748b;
}

.sidebar ul p {
  color: #fff;
  padding: 10px;
}

.sidebar ul li a {
  padding: 10px;
  font-size: 1.1em;
  display: block;
}
.sidebar ul li p {
  margin: 0;
  font-size: 1.1em;
  display: block;
  cursor: pointer;
  font-weight: 400;
}

.sidebar ul li p:hover {
  color: #7386d5;
  background: #fff;
  cursor: pointer;
}

.sidebar ul li.active > p,
a[aria-expanded="true"] {
  color: #fff;
  background: #6d7fcc;
}

a[data-toggle="collapse"] {
  position: relative;
}

.dropdown-toggle::after {
  display: block;
  position: absolute;
  top: 50%;
  right: 20px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

ul ul a {
  font-size: 0.9em !important;
  padding-left: 30px !important;
  background: #6d7fcc;
}

ul.CTAs {
  padding: 20px;
}

ul.CTAs a {
  text-align: center;
  font-size: 0.9em !important;
  display: block;
  border-radius: 5px;
  margin-bottom: 5px;
}

a.download {
  background: #fff;
  color: #7386d5;
}

a.article,
a.article:hover {
  background: #6d7fcc !important;
  color: #fff !important;
}

/* ---------------------------------------------------
    CONTENT STYLE
----------------------------------------------------- */

#content {
  width: 100%;
  padding: 20px;
  /* padding-left: 240px; */
  min-height: 100vh;
  transition: all 0.3s;
  -webkit-animation: fadein 1.5s;
          animation: fadein 1.5s;
}

/* ---------------------------------------------------
    MEDIAQUERIES
----------------------------------------------------- */

@media (max-width: 768px) {
  #sidebar {
    margin-left: -250px;
  }
  #sidebar.active {
    margin-left: 0;
  }
  #sidebarCollapse span {
    display: none;
  }
}

/* ---------------------------------------------------
    content mine
----------------------------------------------------- */

input[type="text"] {
  width: 80%;
  margin: 5px;
  border: 2px solid #ccc;
  border-radius: 5px;
  transition: 0.5s;
  outline: none;
  padding: 5px;
}
input[type="text"]:focus {
  width: 85%;
  border: 2px solid #555;
}
.dropdown-select {
  width: 50%;
  border: 2px solid #ccc;
  border-radius: 5px;
  /* transition: 0.5s; */
  outline: none;
  margin: 5px;
  padding: 5px;
}
.dropdown-select:focus {
  width: 50%;
}

select {
  width: 80%;
  border: 2px solid #ccc;
  border-radius: 5px;
  transition: 0.5s;
  outline: none;
  margin: 5px;
  padding: 5px;
}
select > option {
  width: 20%;
  background-color: #2422220c;
}
select:focus {
  width: 85%;
  border: 2px solid #555;
}
.btn {
  display: -webkit-flex;
  display: flex;
  width: auto;
  padding: 7px;
  margin-left: 5px;
}
.submit-btn {
  display: -webkit-flex;
  display: flex;
  width: auto;
  padding: 7px;
  margin-left: 5px;
  width: auto;
}
/*---------------------------------------
             pagination
-----------------------------------------*/
.pagination-listview {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 10px;
}

@-webkit-keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.btn:focus,
.btn:active {
  outline: none !important;
  box-shadow: none;
}

